@charset "utf-8";

nav{
  width:1200px;
  margin:0 auto;
}
nav ul{
  display:flex;
  justify-content: space-around;
}
nav ul li{list-style: none;}
nav ul li:before{display: none;}
nav li a{
  font-weight: bold;
  color:#000;
  text-decoration: none;
}
nav li a:hover{
  text-decoration: underline;
  color:#f00
}
