@charset "utf-8";

.movie_list{
  width:1200px;
  margin:0 auto;
}
.movie_list ul{
  text-align: center;
  display:flex;
  flex-wrap:wrap;
  justify-content: space-between;
}
.movie_list li{
  list-style: none;
  width:300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin:10px 0px;
  /* width:calc(100%/4); */
}
.MovieList > ul > li img{
  width:280px;
  height:350px;
}
.MovieList > ul > li span{
  display:block;
  font-size:18px;
  font-weight: bold;
  color:#00f;
}